import React, {useReducer, useState} from 'react'
import {checkAC, homeWorkReducer, sortAC} from './bll/homeWorkReducer'
import SuperButton from '../h4/common/c2-SuperButton/SuperButton'

export type UserType = {
    _id: number,
    name: string,
    age:number
}

const initialPeople:UserType[] = [
    {_id: 0, name: '', age: 3},
    {_id: 1, name: '', age: 66},
    {_id: 2, name: '', age: 16},
    {_id: 3, name: '', age: 44},
    {_id: 4, name: '', age: 40},
    {_id: 5, name: '', age: 55},
]

function HW8() {
    const [people, dispatchPeople] = useReducer(homeWorkReducer,initialPeople) // need to fix any

    // need to fix any
    const finalPeople = people.map((p: UserType) => (
        <div key={p._id}>
            {p.name}, {p.age}
        </div>
    ))

    // const sortUp = () => setPeople(homeWorkReducer(initialPeople, {type: 'sort', payload: 'up'}))

    const sortUp = () => {
        dispatchPeople(sortAC('up'));
    }
    const sortDown = () => {
        dispatchPeople(sortAC('down'));
    }
    const check = () => {
        dispatchPeople(checkAC(18));
    }


    return (
        <div>
            <hr/>
            homeworks 8

            {/*should work ( )*/}
            {finalPeople}

            <div><SuperButton onClick={sortUp}>sort up</SuperButton></div>
            <div><SuperButton onClick={sortDown}>sort down</SuperButton></div>
            <div><SuperButton onClick={check}>check 18</SuperButton></div>

            <hr/>
            {/*  ,  */}
            {/*<AlternativePeople/>*/}
            <hr/>
        </div>
    )
}

export default HW8
